<template>
  <div class="cart">
    <!-- <h1>This is an about page</h1> -->
    <CartSteps v-model:actualStep="actualStep"></CartSteps>
    <main>
      <CartOverview
        v-if="actualStep === 1"
        :cart="cart"
        @next="handleNextStep"
        @removeItem="removeFromCart"
      ></CartOverview>
      <CartRules v-if="actualStep === 2" @next="handleNextStep"></CartRules>
      <PersonalData
        v-if="actualStep === 3"
        v-model:personalData="personalData"
        :warning="warning"
        @next="handleNextStep"
      ></PersonalData>
    </main>
    <footer>
      <button class="next" @click="handleNextStep">
        {{ btnText[actualStep] }}
      </button>
    </footer>
  </div>
</template>

<script>
/* eslint no-underscore-dangle: ["error", { "allow": ["_id"] }] */
import ApiService from '@/services/ApiService';

import CartSteps from '@/components/cart/Steps.vue';
import CartOverview from '@/components/cart/Overview.vue';
import CartRules from '@/components/cart/Rules.vue';
import PersonalData from '@/components/cart/PersonalData.vue';

export default {
  name: 'Cart',
  props: ['rootCart'],
  components: {
    CartSteps,
    CartOverview,
    CartRules,
    PersonalData,
  },

  data() {
    return {
      actualStep: 1,
      btnText: {
        1: 'Pokračovat',
        2: 'Souhlasím s podmínkami',
        3: 'Závazně objednat',
      },
      personalData: {
        firstName: '',
        lastName: '',
        email: '',
        phone: '',
      },
      warning: '',
    };
  },
  methods: {
    removeFromCart({ cartId, itemId }) {
      ApiService.removeFromCart(cartId, itemId)
        .then(({ data }) => {
          console.log({ data });
          this.cart = data.updatedCart;
        })
        .catch((err) => {
          console.error(err);
        });
    },
    handleNextStep() {
      if (this.actualStep < 3) {
        this.actualStep += 1;
      } else {
        console.log('odesílám objednávku');
        this.submitOrder();
      }
    },
    submitOrder() {
      // prettier-ignore
      if (
        this.personalData.firstName
        && this.personalData.lastName
        && this.personalData.email
        && this.personalData.phone
      ) {
        console.log('dokončuji');
        ApiService.submitOrder(this.cart._id, this.personalData)
          .then(({ data }) => {
            console.log(data);
            localStorage.removeItem('cartId');
            this.$router.push('done');
          })
          .catch((err) => {
            console.error(err);
          });
      } else {
        this.warning = 'Rezervaci nelze dokončit, aniž by byly vyplněny všechny údaje';
        setTimeout(() => {
          this.warning = '';
        }, 2500);
      }
    },
  },
  computed: {
    cart: {
      get() {
        return this.rootCart;
      },
      set(newValue) {
        this.$emit('update:rootCart', newValue);
      },
    },
  },
};
</script>

