<template>
  <div class="card">
    <div class="steps">
      <ul role="tablist">
        <li
          v-for="(step, index) in steps"
          :key="index"
          class="steps-item"
          :class="{
            active: activeStep === index + 1,
            disabled: activeStep < index + 1,
          }"
        >
          <a @click.prevent="handleClick(index)" href="" class="menu-item"
            ><span class="steps-number">{{ index + 1 }}</span
            ><span class="steps-title">{{ step }}</span></a
          >
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  name: 'CartSteps',
  props: ['actualStep'],
  data() {
    return {
      steps: ['Rekapitulace', 'Provozní řád', 'Osobní údaje'],
    };
  },
  methods: {
    handleClick(index) {
      if (index + 1 < this.actualStep) {
        this.activeStep = index + 1;
        console.log('yes');
      } else {
        console.log('no');
      }
    },
  },
  computed: {
    activeStep: {
      get() {
        return this.actualStep;
      },
      set(newValue) {
        this.$emit('update:actualStep', newValue);
      },
    },
  },
};
</script>

